* {
	box-sizing: border-box;
}
html, 
body {
	height: 100%;
	margin: 0;
	padding: 0;
	width: 100%;
}
h1, h2, h3, h4, h5, h6 {
	text-transform: uppercase;
	margin: 0 0 1rem;
	padding: 0;
	font-weight: 500;
	line-height: 1.2;
}
h1 {
	font-size: 2rem;
}
h2 {
	font-size: 2rem;
}
h3, h4, h5, h6 {
	color: #6F7685;
}
h3 {
	font-size: 1.75rem;
}
h4 {
	font-size: 1.5rem;
}
h5 {
	font-size: 1.25rem;
}
h6 {
	font-size: 1.1rem;
}
p {
	padding: 0;
	margin: 0 0 1rem;
	line-height: 1.4;
}
a {
	&:link, 
	&:visited {
		color: var(--text-link);
	}
}
.s-icon {
	display: inline-block;
	
	svg {
		vertical-align: top;
		fill: currentColor;
	}
}